import React, { useState, useEffect } from 'react'
import './index.scss'

// ** Reactstrap
import { Button } from 'reactstrap'

// ** Utility
import { isMetaMaskInstalled, switchToHPBNetwork, getHPBPrice, coingeckoPing } from '@utility/metamask'

// ** Use Wallet
import { useWallet, ConnectionRejectedError } from 'use-wallet'


export default function WalletConnection() {
  const wallet = useWallet()

  const [isMetaMask, setMetaMask] = useState(false)
  const [isSwitching, setSwitching] = useState(false)
  const [message, setMessage] = useState('')

  useEffect(() => {
    if (isMetaMaskInstalled()) {
      setMetaMask(true)
    } else {
      setMetaMask(false)
    }
  }, [])

  // useEffect(async () => {
  //   let intervalId
  //   const isSuccess = await coingeckoPing()
  //   if (isSuccess) {
  //     intervalId = setInterval(async () => {
  //       const aa = await getHPBPrice()
  //       console.log(11, aa)
  //     }, 1000)
  //   }
    
  //   return () => {
  //     if (intervalId) {
  //       clearInterval(intervalId)
  //     }
  //     console.log('======= clear ========')
  //   }
  // }, [])

  useEffect(() => {
    const { error, status } = wallet

    if (error?.name) {
      if (error instanceof ConnectionRejectedError) {
        setMessage('User rejected the request.')
      } else {
        setMessage(error.name)
      }
    }

    if (status === 'connecting') {
      setMessage('MetaMask is connecting . . .')
    }

    if (status === 'connected') {
      setMessage('MetaMask was connected.')
    }

    if (status === 'disconnected') {
      setMessage('Please connect MetaMask.')
    }
  }, [wallet])

  const handleConnectMetaMask = () => {
    setSwitching(true)
    setMessage('System is waiting your action . . .')

    switchToHPBNetwork().then(res => {
      setSwitching(false)
      setMessage('MetaMask was switched.')

      if (res && res.status === 200) {
        setTimeout(() => wallet.connect('injected'), 500)
      } else {
        if (res.message.includes('pending')) {
          setMessage('Your request is already pending.')
        } else {
          setMessage(res.message)
        }
      }
    })
  }

  const handleDisconnectMetaMask = () => {
    wallet.reset()
    setMessage('MetaMask was disconnected.')
  }

  return (
    <div className='WalletConnection'>
      <div className='message'><span className='emoji'></span> {message}</div>
      {isMetaMask ? (
        <>
          {wallet.error || isSwitching ? (
            <Button color='primary' onClick={() => wallet.reset()}><span></span> Try again MetaMask</Button>
          ) : (
            <>
              {wallet.status === 'connected' ? (
                <Button color='primary' onClick={() => handleDisconnectMetaMask()}><span></span> Disconnect MetaMask</Button>
              ) : (
                <>
                  <Button color='primary' onClick={() => handleConnectMetaMask()}><span></span> Connect MetaMask</Button>
                </>
              )}
            </>
          )}
        </>
      ) : (
        <a href='https://metamask.io/download.html'>
          Click here to install MetaMask!
        </a>
      )}
      {/* <Button className='popup-button'></Button> */}
    </div>
  )
}