import CoinGecko from "coingecko-api"

const isMetaMaskInstalled = () => {
  const { ethereum } = window
  return ethereum && ethereum.isMetaMask
}

const addHPBNetwork = async () => {
  let res = {}

  const params = [
    {
      chainId: '0x64',
      chainName: 'Gnosis Chain',
      nativeCurrency: {
        name: 'xDai',
        symbol: 'xDai',
        decimals: 18
      },
      rpcUrls: ['https://rpc.xdaichain.com/'],
      blockExplorerUrls: ['https://blockscout.com/xdai/mainnet/']
    }
  ]

  try {
    await window.ethereum.request({ method: 'wallet_addEthereumChain', params })
    res = {
      status: 200,
      message: 'Success!'
    }
  } catch (error) {
    res = {
      status: 401,
      message: `Error: ${error.message}`
    }
  }

  return res
}

const switchToHPBNetwork = async () => {
  let res = {}

  try {
    await ethereum.request({
      method: 'wallet_switchEthereumChain',
      params: [{ chainId: '0x64' }]
    })
    res = {
      status: 200
    }
  } catch (error) {
    // This error code indicates that the chain has not been added to MetaMask.
    if (error.code === 4902) {
      const _res = await addHPBNetwork()
      if (_res.status === 200) {
        res = {
          status: 200
        }
      } else {
        res = {
          status: 401
        }
      }
    } else {
      res = {
        status: 500,
        message: error.message
      }
    }
  }

  return res
}

const addToken = () => {
  const params = {
    type: 'ERC20',
    options: {
      address: '0xd3226b12e6188133b19ac0419f34b0ed5b10f069',
      symbol: 'ESR',
      decimals: 18,
      image: 'https://assets.coingecko.com/coins/images/23280/large/esr.png?1643530883'
    }
  }

  window.ethereum.request({ method: 'wallet_watchAsset', params })
    .then(() => console.log('Success, Token added!'))
    .catch((error) => console.log(`Error: ${error.message}`))
}

const CoinGeckoClient = new CoinGecko()

const coingeckoPing = async () => {
  const { success } = await CoinGeckoClient.ping()
  return success
}

const getHPBPrice = async () => {
  let price = 0

  const { data } = await CoinGeckoClient.simple.price({
    ids: ['xdai'],
    vs_currencies: ['eur', 'gbp', 'usd']
  })
  price = data['xdai'].usd

  return price
}

