import { useState, useEffect } from 'react'
import { NavLink, useHistory, Link } from 'react-router-dom'
import { UncontrolledButtonDropdown, DropdownMenu, DropdownItem, DropdownToggle, UncontrolledDropdown } from 'reactstrap'

// ** firebase
import firebase from 'firebase/app'

// ** Store & Actions
import { useSelector } from 'react-redux'

import navigation from '@src/navigation/horizontal'
import HorizontalNavMenuItems from '@src/@core/layouts/components/menu/horizontal-menu/HorizontalNavMenuItems'

import logo from './assets/logo.png'
import WalletConnection from './WalletConnection/index.jsx'

const CustomNavbar = ({ currentActiveItem, routerProps }) => {
  const history = useHistory()
  const auth = useSelector(state => state.auth)

  const [activeItem, setActiveItem] = useState('/home')
  const [groupActive, setGroupActive] = useState([])
  const [openDropdown, setOpenDropdown] = useState([])
  const [navItems, setNavItems] = useState(navigation)

  useEffect(() => {
    if (auth.isAuthed && auth.user) {
      setNavItems([
        ...navigation,
        {
          id: 'compete',
          title: 'COMPETE',
          icon: null,
          navLink: '/compete'
        },
        {
          id: 'profile',
          title: 'PROFILE',
          icon: null,
          navLink: '/profile'
        }
      ])
    }
  }, [auth])

  // ** On mouse enter push the ID to openDropdown array
  const onMouseEnter = id => {
    const arr = openDropdown
    arr.push(id)
    setOpenDropdown([...arr])
  }

  // ** On mouse leave remove the ID to openDropdown array
  const onMouseLeave = id => {
    const arr = openDropdown
    arr.splice(arr.indexOf(id), 1)
    setOpenDropdown([...arr])
  }

  const mobileAndTabletCheck = () => {
    let check = false
    const a = navigator.userAgent || navigator.vendor || window.opera
    if (/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino|android|ipad|playbook|silk/i.test(a) || /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(a.substr(0, 4))) {
      check = true
    }

    return check
  }

  const [isMobile, setIsMobile] = useState(false)
  useEffect(() => {
    setIsMobile(mobileAndTabletCheck())
  }, [])

  const handleLogout = () => {
    firebase.auth().signOut().then(function () {
      history.push('/signin')
    }).catch(function (error) {
      alert(error.message)
    })
  }

  return (
    <div className="w-100 flex align-center space-between px-2">
      <div id="navbar-logo">
        <div className="grad-anim"></div>
        <NavLink to='/home'>
          <img src={logo} alt="esportsref" />
        </NavLink>
      </div>

      {isMobile ? (
        auth.isAuthed && auth.user ? (
          <UncontrolledDropdown tag='li' className='dropdown-user nav-item'>
            <DropdownToggle href='/' tag='a' className='nav-link dropdown-user-link' onClick={e => e.preventDefault()}>
              <div className='flex align-items-center'>
                <div>
                  <div className='user-name font-weight-bold mr-1'>{auth.user.username}</div>
                  <div className='user-type font-weight-bold mr-1 text-right'>{+auth.user.usertype ? 'referee' : 'player'}</div>
                </div>
                <div className='avatar-image'>
                  <img src={auth.user.avatarURL} width={65} height={65} />
                  <span className="online-status" style={{ backgroundColor: auth.user.isOnline ? '#34D399' : '#888' }}></span>
                </div>
              </div>
            </DropdownToggle>
            <DropdownMenu left="true">
              {
                navItems.map((item) => (
                  <NavLink key={item.id} to={item.navLink}>
                    <DropdownItem>
                      {item.title}
                    </DropdownItem>
                  </NavLink>
                ))
              }
              <DropdownItem tag={Link} to='/signin' onClick={() => handleLogout()}>
                LOGOUT
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        ) : (
          <UncontrolledButtonDropdown >
            <DropdownToggle color='primary' className="px-3 px-sm-5" caret>
              <strong>MENU</strong>
            </DropdownToggle>
            <DropdownMenu>
              {
                navItems.map((item) => (
                  <NavLink key={item.id} to={item.navLink}>
                    <DropdownItem>
                      {item.title}
                    </DropdownItem>
                  </NavLink>
                ))
              }
              <NavLink key='signin' to='/signin' onClick={() => setActiveItem('')}>
                <DropdownItem>
                  SIGN IN / SIGN UP
                </DropdownItem>
              </NavLink>
            </DropdownMenu>
          </UncontrolledButtonDropdown>
        )
      ) : (
        <>
          <ul className="nav navbar-nav d-none d-xl-flex">
            <HorizontalNavMenuItems
              submenu={false}
              items={navItems}
              activeItem={activeItem}
              groupActive={groupActive}
              routerProps={routerProps}
              onMouseEnter={onMouseEnter}
              onMouseLeave={onMouseLeave}
              openDropdown={openDropdown}
              setActiveItem={setActiveItem}
              setGroupActive={setGroupActive}
              setOpenDropdown={setOpenDropdown}
              currentActiveItem={currentActiveItem}
            />
          </ul>
          <div>
            {auth.isAuthed && auth.user ? (
              <div className="flex">
                {/* <div className='position-relative'>
                  <Badge pill color='primary' className='badge-up'>
                    5
                  </Badge>
                  <Bell className='text-primary' size={22} />
                </div> */}
                <UncontrolledDropdown tag='li' className='dropdown-user nav-item'>
                  <DropdownToggle href='/' tag='a' className='nav-link dropdown-user-link' onClick={e => e.preventDefault()}>
                    <div className='flex align-items-center'>
                      <div>
                        <div className='user-name font-weight-bold mr-1'>{auth.user.username}</div>
                        <div className='user-type font-weight-bold mr-1 text-right'>{+auth.user.usertype ? 'referee' : 'player'}</div>
                      </div>
                      <div className='avatar-image'>
                        <img src={auth.user.avatarURL} width={65} height={65} />
                        <span className="online-status" style={{ backgroundColor: auth.user.isOnline ? '#34D399' : '#888' }}></span>
                      </div>
                    </div>
                  </DropdownToggle>
                  <DropdownMenu left='true'>
                    <DropdownItem tag={Link} to='/profile' className='pr-10'>
                      PROFILE
                    </DropdownItem>
                    <DropdownItem tag={Link} to='/signin' className='pr-10' onClick={() => handleLogout()}>
                      LOGOUT
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              </div>
            ) : (
              <NavLink to='/signin'>
                <span className="login-button">SIGN IN</span>
              </NavLink>
            )}
          </div>
        </>
      )}
      <WalletConnection />
    </div>
  )
}

export default CustomNavbar
