// ** Router Import
import React, { useEffect } from 'react'
import Router from './router/Router'
import { useDispatch } from 'react-redux'
import { handleLogin, handleLogout, handleOnlineStatus } from '@store/actions/auth'
import firebase from 'firebase/app'

const App = () => {
  const dispatch = useDispatch()

  useEffect(() => {
    let userStatusRef
    
    firebase.auth().onAuthStateChanged(res => {
      if (res) {
        const uid = res.uid
        userStatusRef = firebase.database().ref(`/users/${uid}`)

        userStatusRef
          .onDisconnect()
          .update({
            isOnline: false
          })
          .then(() => {
            userStatusRef.update({
              isOnline: true
            })
            dispatch(handleOnlineStatus(true))
          })

        userStatusRef
          .once('value')
          .then((snapshot) => {
            const user = snapshot.val()
            if (user && Object.keys(user).length) {
              dispatch(handleLogin(user))
            }
          })
      } else {
        if (userStatusRef) {
          userStatusRef.update({
            isOnline: false
          })

          userStatusRef.off()
        }
        dispatch(handleLogout())
      }
    })
  }, [])

  return <Router />
}

export default App
