import { authConsts } from '../../consts'

export const setAuth = (isAuthed) => ({ type: authConsts.SET_AUTH, payload: { isAuthed } })

export const changeAuth = (isAuthed) => dispatch => {
  localStorage.setItem('isAuthed', isAuthed)
  dispatch(setAuth(isAuthed))
}

// ** Handle User Login
export const handleLogin = user => {
  return dispatch => {
    dispatch({ type: authConsts.LOG_IN, payload: { user } })
    dispatch(changeAuth(true))
  }
}

// ** Handle User Logout
export const handleLogout = () => {
  return dispatch => {
    dispatch({ type: authConsts.LOG_OUT })
    dispatch(changeAuth(false))
  }
}

// ** Update User
export const handleUpdateUser = (updatedUser) => {
  return dispatch => {
    dispatch({ type: authConsts.UPDATE_USER, payload: { updatedUser } })
  }
}

// ** Update online status
export const handleOnlineStatus = (isOnline) => {
  return dispatch => {
    dispatch({ type: authConsts.UPDATE_ONLINE_STATUS, payload: { isOnline } })
  }
}