import { lazy } from 'react'

// ** Document title
const TemplateTitle = '%s - Vuexy React Admin Template'

// ** Default Route
const DefaultRoute = '/home'

// ** Merge Routes
const Routes = [
  {
    path: '/home',
    component: lazy(() => import('../../views/Home/index.js'))
  },
  {
    path: '/compete',
    component: lazy(() => import('../../views/Compete')),
    meta: {
      authRoute: true
    }
  },
  {
    path: '/games',
    component: lazy(() => import('../../views/Games/index.js'))
  },
  {
    path: '/become-a-ref',
    component: lazy(() => import('../../views/BecomeRef'))
  },
  {
    path: '/contact',
    component: lazy(() => import('../../views/Contact'))
  },
  {
    path: '/faq',
    component: lazy(() => import('../../views/Faq'))
  },
  {
    path: '/news',
    component: lazy(() => import('../../views/News'))
  },
  {
    path: '/signin',
    component: lazy(() => import('../../views/Signin')),
    layout: 'LoginLayout'
  },
  {
    path: '/signup',
    component: lazy(() => import('../../views/Signup')),
    layout: 'LoginLayout'
  },
  {
    path: '/forgot-password',
    component: lazy(() => import('../../views/ForgotPassword')),
    layout: 'LoginLayout'
  },
  {
    path: '/profile',
    component: lazy(() => import('../../views/Profile')),
    meta: {
      authRoute: true
    }
  },
  {
    path: '/xdai/:tokenContractAddress',
    component: lazy(() => import('../../views/HPB'))
  },
  {
    path: '/error',
    component: lazy(() => import('../../views/Error')),
    layout: 'BlankLayout'
  }
]

export { DefaultRoute, TemplateTitle, Routes }
