import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { useCollection } from 'react-firebase-hooks/firestore'
import { Button } from 'reactstrap'

// ** Utility
import { firestore, auth as fireAuth } from '@utility/firebase'

const db = firestore.collection('invites')

export default function InviteNotification() {
  const history = useHistory()

  const userId = fireAuth.currentUser?.uid
  const [value] = useCollection(firestore.collection('invites'))
  const [invites, setInvites] = useState([])

  useEffect(() => {
    if (userId && value) {
      db.onSnapshot(snapshot => {
        if (snapshot.empty) {
          setInvites([])
          return
        }

        db.where('status', '==', 'created').where('player2', '==', userId).get()
          .then((dt) => {
            if (dt.empty) {
              setInvites([])
              return
            }
            setInvites(dt)
          })
      }, err => {
        console.log(`Encountered error: ${err}`)
      })
    }
  }, [userId, value])

  const handleAccept = ([key, { tokenType, tokenContractAddress }]) => {
    db.doc(key).update({ status: 'accepted' })
    history.push(`/${tokenType}/${tokenContractAddress}`)
  }

  const handleReject = ([key]) => {
    db.doc(key).update({ status: 'rejected' })
  }

  if (invites) {
    const obj = {}

    invites.forEach(doc => {
      const id = doc.id
      const data = doc.data()

      obj[id] = { ...data }
    })

    return (
      <div className='invite-notification-widget'>
        {
          Object.entries(obj).map((data) => (
            <div key={data[0]}>
              Player <span>{data[1].player1Username}</span> has invited you to their game
              <Button color='primary' onClick={() => handleAccept(data)}>Accept</Button>
              <Button color='danger' onClick={() => handleReject(data)}>Reject</Button>
            </div>
          ))
        }
      </div>
    )
  }

  return <></>
}